.ui-fade {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 200px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, #222 100%);
}

.ui-banner {
  width: 100%;
  height: 400px;
  image-rendering: pixelated;
  object-fit: cover;
  object-position: top;
  display: block;
}

.ui-grid-tiny {
  padding-left: 5px !important;
}

.ui-logo-small {
  width: 16px;
  height: 16px;
}

.ui-logo-big {
  width: 32px;
  height: 32px;
}